export default {
	data() {
		return {
			order: '',
			type: '',
			mainItem: []
		};
	},
	onShow() {
		this.$store.commit('judgeLogin'); //判断登录状态
		this.getAddress()
	},
	onLoad(option) {
		if (option.order) this.order = option.order;
		if (option.type) this.type = option.type;
		this.getAddress();
	},
	methods: {
		getAddress() {
			//获取收货地址
			var _this = this;
			uni.showLoading({
				title: '数据查询中'
			});
			//获取用户信息
			var _this = this;
			_this.$request.address
				.gradeList({})
				.then(data => {
					_this.$tools.loadingHide();
					if (data.status == 1) {
						_this.mainItem = data.data;
					} else {
						_this.$tools.toast(data.msg)
					}
				})
				.catch(err => {
					_this.$tools.loadingHide();
					//消息异常
					_this.$tools.toast('数据加载异常')
				});
		},
		add() {
			uni.navigateTo({
				url: '/pages/address/add?order=' + this.order + '&type=' + this.type
			});
		},
		del(id) {
			var _this = this;
			uni.showModal({
				title: '提示',
				content: '是否确认删除此收货地址？',
				success: function(res) {
					if (res.confirm) {
						uni.showLoading({
							title: '数据查询中'
						});
						_this.$request.address
							.delAddress({
								address_id: id
							})
							.then(data => {
								if (data.status == 1) {
									_this.getAddress();
								} else {
									_this.$tools.toast(data.msg);
								}
								_this.$tools.loadingHide();
							})
							.catch(err => {
								_this.$tools.loadingHide();
								//消息异常
								_this.$tools.toast('数据加载异常');
							});
					} else if (res.cancel) {}
				}
			});
		},
		update(item) {
			uni.navigateTo({
				url: '/pages/address/add?order=' + this.order + '&type=' + this.type +
					"&id=" + item.id +
					"&receiving_name=" + item.receiving_name +
					"&receiving_phone=" + item.receiving_phone +
					"&province=" + item.province +
					"&city=" + item.city +
					"&area=" + item.area +
					"&detailed_address=" + item.detailed_address +
					"&default=" + item.default
			});
		},
		setdefault(id) {
			var _this = this;
			_this.$request.address
				.defaulst({
					address_id: id
				})
				.then(data => {
					if (data.status == 1) {
						_this.getAddress();
					} else {
						_this.$tools.toast(data.msg)
					}
					_this.$tools.loadingHide();
				})
				.catch(err => {
					_this.$tools.loadingHide();
					//消息异常
					_this.$tools.toast('数据加载异常')
				});
		},
		selectAddress(item) {
			if (this.order) {
				if (this.type == "product") {
					uni.navigateTo({
						url: '/pages/product/confirmOrder?order=' + this.order + '&addressId=' + item.id
					});
				} else if (this.type == "shop") {
					uni.navigateTo({
						url: '/pages/shop/confirmOrder?order=' + this.order + '&addressId=' + item.id
					});
				}
			}
		}
	}
};
